// https://lofimusic.app/

import React, { createRef, useEffect } from 'react';
import ReactDOM from 'react-dom';
import '@atlaskit/css-reset';
import { ArtistLists , current } from './ArtistLists';
import ChannelLink from './Channel';
import SupportLink from './Support';
import { PlayButton, RandomButton, VolumeButton } from './Button';
import YouTube from 'react-youtube';
import './Fonts.css'
import './App.css'

class App extends React.Component {
	constructor(props) {
		super(props);

		this.state = JSON.parse(window.localStorage.getItem('dataLofi'));

		if (this.state === null || this.state === undefined) {
			this.state = current;
			window.localStorage.setItem('dataLofi', JSON.stringify(this.state));
		}
		
		this.OnWindowResize = this.OnWindowResize.bind(this);
		this.SetLocalStorage = this.SetLocalStorage.bind(this);
		this.OnSelectChannel = this.OnSelectChannel.bind(this);
		this.RequestChengePlayerState = this.RequestChengePlayerState.bind(this);
		this.GetPlayerState = this.GetPlayerState.bind(this);
		this.RequestChangeMuteState = this.RequestChangeMuteState.bind(this);
		this.IsPlayerMute = this.IsPlayerMute.bind(this);
		this.OnVolumeChange = this.OnVolumeChange.bind(this);
		this.OnReady = this.OnReady.bind(this);
		this.OnStateChange = this.OnStateChange.bind(this);
		this.RequestRandom = this.RequestRandom.bind(this);
		this.OnNavButtonClick = this.OnNavButtonClick.bind(this);
		this.OnGlobalClick = this.OnGlobalClick.bind(this);
		this.play = createRef();
		this.volume = createRef();
		this.panel = createRef();
		this.navButton = createRef();
		this.navBG = createRef();
		this.controls = createRef();
		this.isNavHidden = true;

		window.addEventListener('resize', this.OnWindowResize);
		window.addEventListener('click', this.OnGlobalClick)
		
	}

	componentDidUpdate() {
		if (this.state.player !== undefined) {
			if (window.innerWidth < 1042)
			{
				this.panel.current.classList.add('panel-hidden');
			} else {
				this.panel.current.classList.remove('panel-hidden');
			}
		}
	}

	OnGlobalClick(event) {
		// get z-index of target
		if (window.innerWidth > 1042) return;
		
		let zIndex = window.getComputedStyle(event.target).getPropertyValue('z-index');
		if (zIndex !== "auto" && this.isNavHidden === false)
		{
			this.panel.current.classList.toggle('panel-hidden');
			this.navButton.current.classList.toggle('panel-hidden');
			this.navBG.current.classList.toggle('panel-hidden');
			this.controls.current.classList.toggle('panel-hidden');
			this.isNavHidden = true;
		}

	}

	OnWindowResize() {
		if (this.state.player !== undefined) {
			if (window.innerWidth < 1042)
			{
				this.panel.current.classList.add('panel-hidden');
				this.navButton.current.classList.remove('panel-hidden');
			} else {
				this.panel.current.classList.remove('panel-hidden');
				this.navButton.current.classList.add('panel-hidden');
			}
		}
	}

	SetLocalStorage() {
		window.localStorage.setItem('dataLofi', JSON.stringify({
			...this.state,
			player: undefined,
		}));
	}

	OnReady(event) {
		this.setState({
			...this.state,
			status: false,
			player: event.target,
		});
		this.state.player.setVolume(this.state.volume);
		if (this.state.muted) {
			this.state.player.mute();
			this.volume.current.CheckState();
		}
		this.play.current.CheckState();
	}

	OnStateChange(event) {
		this.play.current.CheckState();
		let state = this.GetPlayerState();
		if (state === 0) {
			state = false;
			if (window.innerWidth < 1042) this.navButton.current.classList.add('panel-hidden');
		} else {
			this.navBG.current.classList.add('panel-hidden');
			if (window.innerWidth < 1042) this.navButton.current.classList.remove('panel-hidden');
			state = true;
		}
		this.setState({
			...this.state,
			playing: state,
		});

		this.SetLocalStorage();
	}

	OnSelectChannel(item) {
		this.setState({
			...this.state,
			playing: false,
			name: item.name,
			url: item.url,
		});
		if (window.innerWidth > 1042) return;
		this.panel.current.classList.toggle('panel-hidden');
		this.navButton.current.classList.toggle('panel-hidden');
		this.navBG.current.classList.toggle('panel-hidden');
		this.controls.current.classList.toggle('panel-hidden');
		this.isNavHidden = !this.isNavHidden;
	}

	RequestChengePlayerState() {
		if (this.GetPlayerState() != 1) {
			this.state.player.playVideo();
			this.navBG.current.classList.add('panel-hidden');
		} else {
			this.state.player.pauseVideo();
			this.navBG.current.classList.remove('panel-hidden');
		}
		return this.GetPlayerState();
	}

	GetPlayerState() {
		if (this.state.player.getPlayerState() != 1) {
			return 0;
		} else {
			return 1;
		}
	}

	RequestChangeMuteState() {
		if (this.state.player.isMuted()) {
			this.state.player.unMute();
		} else {
			this.state.player.mute();
		}
		this.state.muted = !this.state.player.isMuted();
		this.SetLocalStorage();
		return this.state.player.isMuted();
	}

	RequestRandom() {
		let index = Math.floor(Math.random() * ArtistLists.length);
		this.OnSelectChannel(ArtistLists[index]);
	}

	IsPlayerMute() {
		return this.state.player.isMuted()
	}

	OnVolumeChange(event) {
		this.state.player.setVolume(event.target.value);
		this.setState({
			...this.state,
			volume: parseInt(event.target.value),
		});

		this.SetLocalStorage();
	}

	OnNavButtonClick() {
		if (window.innerWidth > 1042) return;
		this.panel.current.classList.toggle('panel-hidden');
		this.navButton.current.classList.toggle('panel-hidden');
		this.navBG.current.classList.toggle('panel-hidden');
		this.controls.current.classList.toggle('panel-hidden');
		this.isNavHidden = !this.isNavHidden;
	}

	render() {
			return (
				<div>
					<div className='fill' style={{
						position: "absolute",
						top: 0,
						left: 0,
						overflow: "hidden",
					}}>
					<div style={{
						display: "flex",
						width: "100%",
						height: "100%",
						overflow: "hidden"
					}}>
					<div ref={this.panel} style={{
						position: "relative",
						display: "block",
						flexShrink: 0,
						flexBasis: "282px",
						overflow: "hidden",
					}}>
					<div className='nav fill'>
					<div className='app-title hspace-out' style={{
						display: "flex",
						justifyContent: "flex-start",
						alignItems: "center",
						zIndex: 15,
					}}>
						{/* Header */}
						<header><a className='hApp noselect'>lofimusic</a></header>
					</div>
					<nav className='nav-content' style={{ zIndex: 15 }}>
					<div className='nav-radios'>
					<div className='nav-radios-stack' style={{
						display: "flex",
						justifyContent: "flex-start",
						alignItems: "center",
						zIndex: 15,
					}}>
					<div className='hspace-out'>
						{/* Channel */}
						{ArtistLists.map((item) => {
							return <ChannelLink key={item.name} state={this.state} item={item} OnSelectChannel={this.OnSelectChannel} />
						})}
					</div>
					</div>
					</div>
					<div className='nav-support hspace-out'>
						{/* Footer */}
						<SupportLink type="coffee" text="Buy me a coffee" />
						<SupportLink type="cryptos" text="donate cryptos" />
					</div>
					</nav>
					</div>
					</div>
					</div>
					</div>
					<div className='youtube radio-player fill'>
						<div className='youtube-video' style={{ zIndex: 1 }}>
						{(() => {
							const opts = {
								height: '360',
								width: '640',
								playerVars: {
									modestbranding: 1,
									disablekb: 1,
									iv_load_policy: 3,
									playsinline: 1,
									autoplay: 1,
									controls: 0,
									enablejsapi: 1,
									widgetid: 1
								},
							};
							return <YouTube id='youtube-player' className='noselect' videoId={this.state.url} opts={opts} onStateChange={this.OnStateChange} onReady={this.OnReady} />
						})()}
						</div>
						<div ref={this.controls} className='youtube-controls vspace-bottom vspace-top hspace-out' style={{
							display: "flex",
							justifyContent: "center",
							alignItems: "center",
						}}>
							<div className='youtube-left-space'></div>
							<PlayButton ref={this.play} GetPlayerState={this.GetPlayerState} RequestChengePlayerState={this.RequestChengePlayerState} />
							<RandomButton RequestRandom={this.RequestRandom} state={this.state} />
							<VolumeButton ref={this.volume} state={this.state} IsPlayerMute={this.IsPlayerMute} RequestChangeMuteState={this.RequestChangeMuteState} />
							<div className='youtube-volume'>
								<input onChange={this.OnVolumeChange} id='youtube-volume' defaultValue={this.state.volume} type="range" min="0" max="100"></input>
							</div>
						</div>
					</div>
					<div style={{
						display: "block",
						position: "absolute",
						top: 0,
						left: 0,
						cursor: "pointer",
						zIndex: 15,
					}}><div ref={this.navButton} onClick={this.OnNavButtonClick} className='panel-hidden noselect goapp-shell-hamburger-button-default'>☰</div></div>
					<div ref={this.navBG} className='fill' style={{
						position: "absolute",
						top: 0,
						left: 0,
						backgroundColor: "black",
						opacity: 0.95,
						zIndex: 5,
					}}></div>
				</div>
			);
	}
}

ReactDOM.render(
	<React.StrictMode>
		<App />
	</React.StrictMode>,
	document.getElementById('root')
);