import React, { useState } from "react";

class PlayButton extends React.Component {
	constructor(props) {
		super(props);
		this.onClick = this.onClick.bind(this);
		this.CheckState = this.CheckState.bind(this);
		this.state = {
			r: "18px",
			d: "M8,5.14V19.14L19,12.14L8,5.14Z",
		}
	}

	CheckState() {
		const state = this.props.GetPlayerState();
		if (state === 0) {
			this.setState({
				...this.state,
				d: "M8,5.14V19.14L19,12.14L8,5.14Z",
			})
		} else {
			this.setState({
				...this.state,
				d: "M14,19H18V5H14M6,19H10V5H6V19Z",
			})
		}
	}

	onClick(event) {
		const state = this.props.RequestChengePlayerState();
		if (state === 0) {
			this.setState({
				...this.state,
				d: "M8,5.14V19.14L19,12.14L8,5.14Z",
			})
		} else {
			this.setState({
				...this.state,
				d: "M14,19H18V5H14M6,19H10V5H6V19Z",
			})
		}
	}
	
	render() {
		return (
			<button className="control" onClick={this.onClick}>
				<div style={{
					width: this.state.r,
					height: this.state.r,
				}}>
					<svg style={{
						width: this.state.r,
						height: this.state.r,
					}} viewBox="0 0 24 24">
						<path fill="currentColor" d={this.state.d}></path>
					</svg>
				</div>
			</button>
		)
	}
}

