import React from "react";
import styled from "styled-components";

const Container = styled.a`
display: block;
padding: 3px 0;
cursor: pointer;
transition: 0.2s all;
color: currentColor;
`

export default class ChannelLink extends React.Component {
	constructor(props) {
		super(props);
		this.OnChannelClick = this.OnChannelClick.bind(this);
	}

	OnChannelClick(event) {
		this.props.OnSelectChannel(this.props.item);
	}
	
	render() {
		return (
			<Container className={(() => {
				var className = "link noselect heading glow";
				if (this.props.item.name === this.props.state.name) {
					className += " focus";
				}
				return className;
			})()} onClick={this.OnChannelClick}>
				<div style={{
					display: "flex",
					justifyContent: "flex-start",
					alignItems: "center",
				}}>
					<div className="link-icon">
						<div style={{
							width: "24px",
							height: "24px",
						}}>
							<svg style={{
								width: "24px",
								height: "24px",
							}} viewBox="0 0 24 24">
							<path fill="currentColor" d="M8,5.14V19.14L19,12.14L8,5.14Z"></path>
							</svg>
						</div>
					</div>
					<div>{this.props.item.name}</div>
				</div>
			</Container>
		);
	}
}